}

.vendorCover {
    object-fit: cover
}

.vendorHeader {
    position: relative
}

.vendorHeader:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.5)
}

.vendorHeader.empty:after {
    width: 0;
    height: 0
}

.vendorBack {
    position: absolute;
    top: 12px;
    left: 16px;
    display: flex;
    align-items: center;
    color: #fff;
    cursor: pointer;
    z-index: 1
}

.vendorBack.black {
    color: #000
}

.vendorHeader .vendor-item__logo {
    z-index: 1
}

.vendorBack .backText {
    margin-left: 8px
}

.vendorTitle {
    font-weight: 700;
    font-size: 30px;
