}

.vendorRating {
    display: flex;
    align-items: center;
    padding: 18px 0;
    position: relative
}

.vendorRatingCount {
    margin-left: 12px
}

.vendorRating:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 1px;
    width: calc(100% + 16px);
    background: #e3e4e6
}

.vendorDescription {
    color: #919399;
    font-size: 12px;
    line-height: 16px;
    margin-top: 8px
}

.vendorDeliveryInfo {
    padding: 18px 0;
