    align-items: center
}

.vendorDeliveryInfo .deliveryTime {
    font-size: 12px;
    font-weight: 700;
    padding: 2px 5px;
    border-radius: 4px;
    background-color: #919399;
    color: #fff;
    white-space: nowrap
}

.vendorDeliveryInfo .deliveryTime.is-express {
    background-color: #0ec645
}

.vendorDeliveryInfo .deliveryInfo {
    margin-left: 16px;
    font-size: 15px;
    color: #919399;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis
}

